import React from "react";
import "./home.css";
import Grid from "@material-ui/core/Grid";
import Hidden from "@material-ui/core/Hidden";
import "react-awesome-slider/dist/styles.css";
import Button from "@material-ui/core/Button";
import { BsEnvelope } from "react-icons/bs";
import { Link } from "react-router-dom";
import { SiPolymerproject } from "react-icons/si";
import { EN, FA } from "../../../Data/lang-data";
import PresentCard from "../../PresentCard";
import { useState } from "react";
function Home(props) {
    const { langToggle, setActiveIcon } = props;
    const [showCard, setShowCard] = useState(false);
    return (
        <>
            <Grid className="home-grid-container" container spacing={1}>
                <Grid
                    className="left-wrapper"
                    item
                    xs={12}
                    md={6}
                    lg={6}
                    onMouseOver={() => setShowCard(false)}
                >
                    {langToggle ? EN.homeIntro : FA.homeIntro}

                    {langToggle ? EN.homeOutro : FA.homeOutro}

                    <div className="home-buttons-container" dir="ltr">
                        <Button
                            className="home-contact-btn"
                            variant="outlined"
                            color="secondary"
                            startIcon={<BsEnvelope />}
                            size="large"
                        >
                            <Link to="/contact">
                                {langToggle ? EN.contactBtn : FA.contactBtn}
                            </Link>
                        </Button>
                        <Button
                            className="home-contact-btn"
                            variant="contained"
                            color="default"
                            startIcon={<SiPolymerproject />}
                            size="large"
                        >
                            <Link to="/projects">
                                {langToggle ? EN.projectsBtn : FA.projectsBtn}
                            </Link>
                        </Button>
                    </div>
                </Grid>
                <Grid className="right-wrapper" item xs={12} md={6} lg={6}>
                    <div className="circles">
                        <img
                            loading="eager"
                            onMouseOver={() => setShowCard(true)}
                            className="circle-img"
                            src={
                                process.env.PUBLIC_URL + "/assets/bigCircle.png"
                            }
                            alt=""
                        />
                        <img
                            loading="eager"
                            onMouseOver={() => setShowCard(true)}
                            className="small-circle-img"
                            src={
                                process.env.PUBLIC_URL +
                                "/assets/smallCircle.png"
                            }
                            alt=""
                        />
                    </div>
                    <Hidden smDown>
                        {showCard && (
                            <PresentCard
                                setActiveIcon={setActiveIcon}
                                langToggle={langToggle}
                            />
                        )}
                    </Hidden>
                </Grid>
            </Grid>
        </>
    );
}

export default Home;
