import React, { useEffect } from "react";
import "./index.css";
import { GrReactjs } from "react-icons/gr";
import { VscDeviceMobile } from "react-icons/vsc";
import { BsArrowRight, BsArrowLeft } from "react-icons/bs";
import { Link } from "react-router-dom";
function PresentCard(props) {
    const { setActiveIcon, langToggle } = props;
    useEffect(() => {
        const card = document.querySelector(".pc-card");
        const container = document.querySelector(".pc-container");

        //Items

        const title = document.querySelector(".pc-title");
        const pic = document.querySelector(".pc-pic img");
        const description = document.querySelector(".pc-info h3");
        const circle = document.querySelector(".pc-bg-circle");
        const btn = document.querySelector(".pc-btn-container");
        container.addEventListener("mousemove", (e) => {
            let xAxis = (window.innerWidth / 2 - e.pageX) / -30;
            let yAxis = (window.innerWidth / 4 - e.pageY) / 15;

            card.style.transform = `rotateY(${yAxis}deg) rotateX(${xAxis}deg)`;
        });

        // Animate In
        container.addEventListener("mouseenter", (e) => {
            card.style.transition = "none";
            //Popout
            circle.style.transform = "translateZ(50px)";
            title.style.transform = "translateZ(60px)";
            pic.style.transform = "translateZ(150px) ";
            description.style.transform = "translateZ(30px)";
            btn.style.transform = "translateZ(30px)";
        });

        // Animate Out
        container.addEventListener("mouseleave", (e) => {
            card.style.transition = "all 0.5s ease";
            card.style.transform = `rotateY(0deg) rotateX(0deg)`;

            //Popback
            title.style.transform = "translateZ(0px)";
            pic.style.transform = "translateZ(0px) rotateZ(0deg)";
            description.style.transform = "translateZ(0px)";
            circle.style.transform = "translateZ(0px)";
            btn.style.transform = "translateZ(0px)";
        });
    }, []);

    return (
        <>
            <div className="pc-container">
                <div className="pc-card">
                    <div className="pc-pic">
                        <div className="pc-bg-circle"></div>
                        <img src={process.env.PUBLIC_URL + "/ff.png"} alt="" />
                    </div>
                    <div className="pc-info">
                        <h1 className="pc-title">
                            {langToggle ? "Faez Taghavi" : "فائز تقوی"}
                        </h1>
                        <h3 className="pc-desc">
                            {langToggle
                                ? "What you want me for?"
                                : "منو برای چی میخوای؟"}
                        </h3>
                    </div>
                    <div className="pc-btn-container">
                        <a href="mailto:faez.taghavi@gmail.com?subject= Web Development">
                            <GrReactjs color="#00a2ff" size={40} />
                            <p>
                                {langToggle ? " Web Development" : "توسعه وب"}
                            </p>
                        </a>
                        <a href="mailto:faez.taghavi@gmail.com?subject=Mobile Programming">
                            <VscDeviceMobile color="#000" size={40} />
                            <p>
                                {langToggle
                                    ? "Mobile Programming"
                                    : "برنامه نویسی موبایل"}
                            </p>
                        </a>
                    </div>
                    <Link
                        to="/skills"
                        className="pc-brain-wrapper"
                        onClick={() => setActiveIcon("Skills")}
                    >
                        <img
                            loading="eager"
                            className="pc-brain"
                            src={process.env.PUBLIC_URL + "/assets/brain.png"}
                            alt=""
                        />
                        {langToggle
                            ? "See Other Skills"
                            : "بیا بقیه مهارتامو ببین"}
                        {langToggle ? (
                            <BsArrowRight color="#fff" size={30} />
                        ) : (
                            <BsArrowLeft color="#fff" size={30} />
                        )}
                    </Link>
                </div>
            </div>
        </>
    );
}

export default PresentCard;
